@use '../../scss/' as *;

.modal{
    @include flex(center,center);
    position:fixed;
    z-index:100;

    top: 0;
    bottom: 0;
    right: 0;
    left:0;
    overflow: auto;
    visibility:hidden;
    opacity: 0;
    background-color: rgba($color: $black, $alpha: 0.4);

    &.active{
        visibility:visible;
        opacity: 1;
    }
    &__content{
        padding: 2rem;
        background-color: $body-bg;
        width: 50%;
        opacity: 0;
        transform: translateY(-250px);
        transition: transform .6s ease,opacity .6s ease;
        position: relative;


        &__close{
            position: absolute;
            right: 5px;
            top: 5px;
            font-size: 1.5rem;
            cursor: pointer;

            &:hover{
                color:$main-color;
            }
        }
    }

    &.active &__content{
        opacity: 1;
        transform: translateY(0);
        
    }
    
}